
.about-container {
    margin-top: 100px;
    width: 40%;
    padding: 40px;
    justify-content: center;
    background-color: #E8E6CD;
    border-radius: 10px;
    font-size: 20px;
    color: #734C2B;
    box-shadow: 15px 15px 6px rgba(0, 0, 0, 0.1);
  }

   /* Responsive Design */
   @media (max-width: 768px) {
    .about-container {
      margin-top: 30px;
      width: 80%;
    }
}